import React, { Component } from 'react';
import {
  Dialog,
  TextField,
  FlatButton,
} from 'material-ui';
import { red600 } from 'material-ui/styles/colors';

class EditNewsDialog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      content: '',
      place: '',
      time: '',
      title: '',
      contentErr: '',
      placeErr: '',
      timeErr: '',
      titleErr: '',
    } 
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      contentErr: '',
      placeErr: '',
      timeErr: '',
      titleErr: '',
      content: nextProps.data.content ,
      place: nextProps.data.place,
      time: nextProps.data.time,
      title: nextProps.data.title,
    })
  }

  editNewsHandler = () => {
    this.setState({
      contentErr: '',
      placeErr: '',
      timeErr: '',
      titleErr: '',
    })
    if (this.state.content !== '' && this.state.place !== '' && this.state.time !== '') {
      this.props.editNews({
        content: this.state.content ,
        place: this.state.place,
        time: this.state.time,
        title: this.state.title,
      })
    }

    if(this.state.title.trim() == '') {
      this.setState({ 
        titleErr: 'Tiu  tin khng c rng'
      });
    }

    if(this.state.time.trim() == '') {
      this.setState({ 
        timeErr: 'Thi gian din ra khng c rng'
      });
    }

    if(this.state.content.trim() == '') {
      this.setState({ 
        contentErr: 'Ni dung tin khng c rng'
      });
    }

    if(this.state.place.trim() == '') {
      this.setState({ 
        placeErr: 'a im khng c rng'
      });
    }
  }

  render() {
    return (
      <Dialog
        title={'Cp nht tin'}
        actions={
          [<FlatButton 
            label={'Hu'} 
            onClick={this.props.handleClose} 
            style={{ 
              color:red600,
              minWidth: '100px', 
              margin: '10px 10px 10px 0px', 
              cursor: 'pointer' }}
          />,
          <FlatButton 
            label={'Cp nht'} 
            onClick={this.editNewsHandler} 
            style={{ 
              minWidth: '100px', 
              margin: '10px 10px 10px 0px', 
              cursor: 'pointer' }}
          />]
        }
        autoScrollBodyContent={true}
        open={this.props.open}
        contentStyle={{ width: '100%' }}
        onRequestClose={this.props.handleClose}>
        <TextField
          name={'decoy'}
          style={{width:'0px',height:'0px'}}/>
        <TextField
          floatingLabelText={'Tiu '}
          onChange={(e) => {
            this.setState({
              title: e.currentTarget.value
            })
          }}
          value={this.state.title}
          errorText={this.state.titleErr}
          fullWidth
        />
        <TextField
          floatingLabelText={'Ni dung'}
          onChange={(e) => {
            this.setState({
              content: e.currentTarget.value
            })
          }}
          value={this.state.content}
          errorText={this.state.contentErr}
          fullWidth
        />
        <TextField
          floatingLabelText={'a im'}
          onChange={(e) => {
            this.setState({
              place: e.currentTarget.value
            })
          }}
          value={this.state.place}
          errorText={this.state.placeErr}
          fullWidth
        />
        <TextField
          floatingLabelText={'Thi gian'}
          onChange={(e) => {
            this.setState({
              time: e.currentTarget.value
            })
          }}
          value={this.state.time}
          errorText={this.state.timeErr}
          fullWidth
        />
      </Dialog>
    );
  }
}

export default EditNewsDialog;


